<template>
  <div id="app">
    <div class="load_box" v-show="loading">
      <div class="load_bg"></div>
      <div class="boxLoading"></div>
    </div>
    <div class="header">邀请信息查询后台</div>
    <div class="main">
      <!-- search -->
      <div class="search">
        <el-row :gutter="20">
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-input placeholder="请输入内容" v-model.trim="inputText" clearable @change="searchEvent"></el-input>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-button type="primary" round @click="searchEvent">查询</el-button>
            </div>
          </el-col>
        </el-row>
      </div>
      <!-- table -->
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column label="酷划ID">
          <template slot-scope="scope">
                  <span>{{ scope.row.coohuaId }}</span>
</template>

</el-table-column>
<el-table-column label="灰名单">
<template slot-scope="scope" title="1111">
  <span :style="{'background-color':scope.row.isgrey==1?'yellow':'',padding:'12px'}">{{ scope.row.isgrey==1?'是':'否' }}</span>
</template>

</el-table-column>
<el-table-column label="手机号">
<template slot-scope="scope">
  <span>{{ scope.row.mobile }}</span>
</template>

</el-table-column>
<el-table-column label="注册时间">
<template slot-scope="scope">
  <span>{{ timeFormat(scope.row.createtime) }}</span>
</template>

</el-table-column>
<el-table-column label="收入">
<template slot-scope="scope">
  <span>{{ scope.row.creditIncome }}</span>
</template>

</el-table-column>
<el-table-column label="支出">
<template slot-scope="scope">
  <span>{{ scope.row.creditOutcome }}</span>
</template>

</el-table-column>
<el-table-column label="安装总收入">
<template slot-scope="scope">
  <span :style="{padding:'12px','background-color':allZero(scope.row.install)?'red':''}">{{ scope.row.install }}</span>
</template>

</el-table-column>
<el-table-column label="日均安装">
<template slot-scope="scope">
  <span>{{ average(scope.row.install,scope.row.createtime) }}</span>
</template>

</el-table-column>
<el-table-column label="左划总收入">
<template slot-scope="scope">
  <span :style="{padding:'12px','background-color':allZero(scope.row.scan)?'red':''}">{{ scope.row.scan }}</span>
</template>

</el-table-column>
<el-table-column label="日均左划">
<template slot-scope="scope">
  <span>{{ average(scope.row.scan,scope.row.createtime) }}</span>
</template>

</el-table-column>
<el-table-column label="右划总收入">
<template slot-scope="scope">
  <span :style="{padding:'12px','background-color':allZero(scope.row.unlock)?'red':''}">{{ scope.row.unlock }}</span>
</template>

</el-table-column>
<el-table-column label="日均右划">
<template slot-scope="scope">
  <span>{{ average(scope.row.unlock,scope.row.createtime) }}</span>
</template>

</el-table-column>
<el-table-column label="邀请">
<template slot-scope="scope">
  <span>{{ scope.row.recommend }}</span>
</template>

</el-table-column>
<el-table-column label="注册">
<template slot-scope="scope">
  <span>{{ scope.row.register }}</span>
</template>

</el-table-column>
<el-table-column label="活动">
<template slot-scope="scope">
  <span>{{ scope.row.activity }}</span>
</template>

</el-table-column>
<el-table-column label="徒弟贡献">
<template slot-scope="scope">
  <span>{{ scope.row.devoteCredit }}</span>
</template>

</el-table-column>
      <el-table-column label="操作">
<template slot-scope="scope" v-if="scope.row.isgrey!=1">
  <el-button size="mini" type="danger" @click="handleGray(scope.$index, scope.row)" style="margin-top:10px;">
    拉灰</el-button>
</template>
      </el-table-column>
    </el-table>
    <div class="line">----- 我是有底线的 -----</div>
    </div>
    <div class="footer">
      <div class="block">
        <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="page_size" layout="prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
// 测试和线上请求地址
var root = process.env.API_ROOT;
root = "http://172.16.10.79:8080";
export default {
  name: "App",
  created() {
    // 初始请求
    this.axiosData(1);
  },
  methods: {
    // coohuaId时刻来自搜索框
    // 每次请求只要传递页码参数
    axiosData(pageNumber) {
      let that = this;
      // 启动loading
      that.loading = true;
      axios
        .get("https://easy-mock.com/mock/5a60391b6c112f4d9e700cc9/information_list/list", {
          params: {
            // coohuaId: this.inputText,
            coohua_id: this.inputText,
            pageNumber: pageNumber, //页号（不是必传，默认为1）
            pageSize: 50
          }
        })
        .then(function(response) {
          // 关闭loading
          that.loading = false;
          let status = response.status;
          if ((status >= 200 && status < 300) || status == 304) {
            that.tableData = response.data.data;
            console.log(response);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 查询内容
    searchEvent() {
      // 搜索请求
      this.axiosData(1);
      // 重置当前页数
      this.currentPage = 1;
    },
    // 当前页面条数
    handleCurrentChange(val) {
      // 指定页数请求
      this.axiosData(val);
    },
    // 拉灰操作
    handleGray(index, row) {
      let that = this;
      // 弹框
      this.$prompt("作弊原因描述", "请输入", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /\S/,
        inputErrorMessage: "描述不能为空"
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: "作弊原因描述: " + value
          });
          // that.axiosGray(value);
          that.$notify({
            title: "成功",
            message: "拉灰成功！",
            type: "success"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消输入"
          });
        });
    },
    // 拉灰请求
    axiosGray(text) {
      let that = this;
      axios
        .post("/api/user/addgrey", {
          params: {
            coohua_id: this.inputText,
            query: {
              reason: text //作弊原因描述
            }
          }
        })
        .then(function(response) {
          let status = response.status;
          if ((status >= 200 && status < 300) || status == 304) {
            // data为0时success为0表示拉灰失败；data为1时success为1表示拉灰成功
            let text = response.data.success;
            if (text == 0) {
              that.$notify.error({
                title: "失败",
                message: "拉灰失败！",
                type: "success"
              });
            } else {
              that.$notify({
                title: "成功",
                message: "拉灰成功！",
                type: "success"
              });
            }
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 注册时间格式化
    timeFormat: function(timestamp) {
      var sDate = new Date(timestamp); //日期
      var sYear = sDate.getFullYear(); //年
      var sMonth = sDate.getMonth() + 1; //月
      var sDay = sDate.getDate(); //日
      var sHour = sDate.getHours(); //小时
      var sMinute = sDate.getMinutes(); //分钟
      var sSecond = sDate.getSeconds(); //秒
      var mSecond = sDate.getMilliseconds(); //毫秒
      return sYear + "-" + this.evenNum(sMonth) + "-" + this.evenNum(sDay);
    },
    // 单数转双数
    evenNum(n) {
      return n < 10 ? "0" + n : "" + n;
    },
    // 日均计算，传递总数和创建时间
    average(allNum, createtime) {
      // 注册天数
      var day = (Date.now() - createtime) / 86400;
      day = day <= 0 ? 1 : day;
      return Math.floor(allNum / day);
    },
    // 判断总收入是否为0
    allZero(n) {
      return n == 0 ? true : false;
    }
  },
  data() {
    return {
      currentPage: 1,
      page_size: 50,
      total: 1000,
      inputText: "806963541",
      tableData: [],
      loading: true
    };
  }
};
</script>

<style>
body {
  margin: 0;
  padding: 0;
  font-family: "helvetica neue", tahoma, "hiragino sans gb", stheiti,
    "wenquanyi micro hei", Microsoft YaHei, SimSun, sans-serif;
  line-height: 1;
}
/* #app {} */
.header {
  background: #37474e;
  color: #fff;
  font-size: 20px;
  height: 60px;
  line-height: 60px;
  padding: 0 24px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 24px;
  border-top: solid 1px #ccc;
  text-align: center;
  background: #fff;
  z-index: 9;
}
.main {
  padding: 60px 24px 50px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
}
.search {
  padding: 12px 30px;
}
.line {
  text-align: center;
  padding: 30px 0;
  text-align: center;
  font-size: 14px;
  color: #ccc;
}
/* Loading CSS 样式 */
.boxLoading {
  width: 50px;
  height: 50px;
  margin: auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 999;
}

.boxLoading:before {
  content: "";
  width: 50px;
  height: 5px;
  background: #000;
  opacity: 0.1;
  position: absolute;
  top: 59px;
  left: 0;
  border-radius: 50%;
  -webkit-animation: box-loading-shadow 0.5s linear infinite;
  animation: box-loading-shadow 0.5s linear infinite;
}
.boxLoading:after {
  content: "";
  width: 50px;
  height: 50px;
  background: #409eff;
  -webkit-animation: box-loading-animate 0.5s linear infinite;
  animation: box-loading-animate 0.5s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 3px;
}

@-webkit-keyframes box-loading-animate {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    -webkit-transform: translateY(9px) rotate(22.5deg);
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    -webkit-transform: translateY(18px) scale(1, 0.9) rotate(45deg);
    transform: translateY(18px) scale(1, 0.9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    -webkit-transform: translateY(9px) rotate(67.5deg);
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    -webkit-transform: translateY(0) rotate(90deg);
    transform: translateY(0) rotate(90deg);
  }
}

@keyframes box-loading-animate {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    -webkit-transform: translateY(9px) rotate(22.5deg);
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    -webkit-transform: translateY(18px) scale(1, 0.9) rotate(45deg);
    transform: translateY(18px) scale(1, 0.9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    -webkit-transform: translateY(9px) rotate(67.5deg);
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    -webkit-transform: translateY(0) rotate(90deg);
    transform: translateY(0) rotate(90deg);
  }
}

@-webkit-keyframes box-loading-shadow {
  0%,
  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(1.2, 1);
    transform: scale(1.2, 1);
  }
}

@keyframes box-loading-shadow {
  0%,
  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(1.2, 1);
    transform: scale(1.2, 1);
  }
}
.load_bg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 998;
}
</style>



